.container {
    display: flex;
    padding: 70px 12%;
  }
  
  .firstChild > div {
    font-size: 40px;
    font-weight: bold;
   text-align: left;
  }
  .emoji {
    display: flex;
    color:hsl(348, 82%, 57%);
  }
  .secondChild {
    flex: 1;
    display: flex;
    justify-content: right;
  }
  .text {
    color:hsl(348, 82%, 57%);
  }
  .secondChild > img {
    display: block;
    height: 380px;
    width: 370px;
    /* border-width:thick; */
    border-color: #594545;
    /* border: 3px solid; */
    
  }
  .para > p {
    font-size:18px;
    text-align: left;
    color:#BA0632;
  }
  .para1 {
    text-align: left;
    color:hsl(348, 82%, 57%);
  }
  .para1 > p {
    /* text-decoration: underline;
    text-decoration-color: #FC7585; */
    color:hsl(348, 83%, 47%);
  }
  .resumeBtn {
    height: 50px;
    width: 25%;
    background-color: #594545;
    cursor: pointer;
    font-size: 17px;
    color: white;
    border: transparent;
    border-radius: 5px;
  }
  
  .iconDiv {
    display: flex;
    gap: 35px;
    margin-top: 10px;
   
  }
  
  a > img {
    height:30px;
    width: 30px;
  }
  
  
  @media only screen and (min-width: 0px) and (max-width: 280px) {
    .container {
      display: block;
    }
    .firstChild > div {
      font-size: 35px;
      font-weight: bold;
      margin-bottom: 10px;
    }
    .secondChild {
      justify-content: center;
    }
    .secondChild > img {
      height: 150px;
      width: 150px;
      margin-top: 50px;
    }
    .resumeBtn {
      height: 20px;
      width: 25%;
      background-color: #594545;
      cursor: pointer;
      font-size: 11px;
      border-radius: 3px;
    }
  }
  
  
