.Github {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  margin: auto;
  background-color: #06102d;
  color: white;
  padding: 5px;
}
.Github1 > a > img {
  width: 60%;
  height: 200px;
  margin: 30px auto;
}

@media only screen and (min-width: 0px) and (max-width: 280px) {
    .Github1 > a > img {
        width: 85%;
        height: 200px;
        margin:auto;
      }
      .Github {
        width: 95%;
      }
}
