.container {
    margin-top:140px;
    padding: 70px 10%;
    height: max-content;

  }
  
  .heading {
    color:hsl(348, 82%, 57%);
    text-align: center;
    font-size: 40px;
    font-weight: bolder;
    margin-bottom: 30px;
  
  }
  
  .childs {
    /* display: grid;
    grid-template-columns: repeat(2,1fr);
    gap:5%; */
  background-color:#f3dede;
    /* padding: 10px; */
    display: flex;
    justify-content: center;
    gap: 8%;
    width: fit-content;
    max-width: 950px;
    border-radius: 10px;
    padding: 10px 10px;
    margin: auto;
    margin-bottom: 35px;
    color:#BA0632;
    font-weight: bolder;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
  .childs:hover {
    border: 1px solid #BA0632;
    box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -webkit-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -moz-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
  }
  .afterImg {
    text-align: left;
  }
  .childs > div {
    margin-top: 10px;
    width:90vw;
  }
  .childs > div > button {
    width: 100px;
    height: 40px;
    background-color: #594545;
    border-radius: 5px;
    border: transparent;
    color: white;
    margin: 0px 10px 20px 10px;
    cursor: pointer;
  }
  
  center > img {
    width: 500px;
    height: 250px;
    object-fit: cover;
  }
  
  .childs > div > button:hover {
    color:rgb(192, 22, 22);
    background-color:white;
    border: 1px solid red;
    box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -webkit-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
    -moz-box-shadow: 1px 1px 17px 5px rgba(255, 98, 37, 0.48);
  }
  
  .childs > div:nth-child(1) {
    align-self: center;
  }
  
  .childs > div > h2 {
    margin: 5px 0px 10px 0px;
  }
  
  ul > li {
    font-size: 15px;
    margin: 7px 0px;
  }
  
  .childs > div > div {
    margin-top: 20px;
  }
  
  @media only screen and (min-width: 0px) and (max-width: 280px) {
    .container {
      padding: 70px 1%;
     
    }
    .childs {
      display: block;
      padding: 10px 4%;
    }
    center > img {
      width: 250px;
      height: 150px;
    }
    ul > li {
      font-size: 12px;
      margin: 7px 0px;
    }
    .childs > div > button {
      width: 30%;
      height: 30px;
      font-size: 12px;
      border-radius: 5px;
      margin-bottom: 10px;
    }
    .childs > div > div {
      font-size: 16px;
    }
    .heading {
      margin-top: 50px;
    }
  }
  
  @media only screen and (min-width: 281px) and (max-width: 320px) {
