*{
  margin: 0px;
  padding: 0px;
}
.container {
    padding: 70px 25%;
  
  }
 .pic {
  display: flex;
  flex-direction: row;
  gap: 16vw;
 }
 .pic > img {
    width:50vw;
    height:50vh;
   float: left;
   margin-left: -160px;
   border-radius: 40px;
  }
  .heading {
    /* color: #4070f4; */
    color:hsl(348, 82%, 57%);
    text-align: center;
    margin-bottom: 40px;
    font-size:40px;
    font-weight: bolder;
  }
  
  .heading2 {
    text-align:left;
    font-size:25px;
    font-weight: bolder;
    margin-top: 25px;
    color:#BA0632;
  }
  
  .para {
    width:30vw;
    text-align:justify;
    font-size:18px;
    line-height: 30px;
    margin-top: 10px;
    color:#BA0632;
    font-weight: bolder;
  }
  .secondChild {
    flex: 1;
    display: flex;
    justify-content: left;
  }
  .mail {
    margin-top: 20px;
    text-align: left;
    color:#BA0632;
  }
  
  .mail > span > img {
    margin-left:300px;
    margin-top: -20px;
    height: 23px;
    width: 23px;
    cursor: pointer;
  }
  
  
